import { reactive } from 'vue'

function fetchRest(method, target, body = null, authToken = null) {
  const headers = { 'Content-Type': 'application/json' }
  if (authToken !== null) headers.Authorization = authToken
  const init = { method, headers }
  if (body !== null) init.body = JSON.stringify(body)
  return fetch(target, init)
}

function extractBody(response, errorMessages = {}) {
  if (response.ok) return response.json()
  throw new Error(errorMessages[response.status] || response.statusText)
}

function basicAuthToken(user) {
  return 'Basic ' + window.btoa(user.name + ':' + user.password)
}

const store = reactive({
    user: {
      name: null,
      password: null,
    },
    isAuthenticated: false,
    history: [],
    signup(name, password) {
      const user = { name, password }
      return fetchRest('POST', '/auth/signup', user)
        .then(response => extractBody(response))
        .then(response => {
          if (!response.success) throw new Error(response.message)
          this.user = user
          this.isAuthenticated = true
        })
    },
    login(name, password) {
      const user = { name, password }
      return fetchRest('GET', '/api/requests', null, basicAuthToken(user))
        .then(response => extractBody(response, { 401: 'Invalid login or password' }))
        .then(response => {
          this.history = response
        })
    },
    submit(x, y, radius) {
      const data = { coordinates: { x, y }, radius }
      return fetchRest('POST', '/api/check', data, basicAuthToken(this.user))
        .then(response => extractBody(response))
        .then(response => {
          this.history.push(response)
        })
    },
    clearHistory() {
      return fetchRest('POST', '/api/clear', null, basicAuthToken(this.user))
        .then(response => {
          if (!response.ok) throw new Error(response.statusText)
        })
    },
    logout() {
      this.user.password = null
      this.isAuthenticated = false
    },
  },
)

export default store
